#wrap_center{

    margin-left:3% ;
    height: calc(100vh - 40px);

}
#body{
    background-color: #E8EAEC;
    font-size: 0.8rem !important;
    overflow: hidden;
}
.center_width_on_open_con{
    width: 81%;
}
.center_width_on_close_con{
    width: 97%;
}
.pointer{
    cursor: pointer;
}
.move{
    cursor: move;
}